<template>
  <div :id="'carouselExampleCaptions-'+carouselId" class="carousel slide" data-bs-ride="carousel" data-bs-touch="false"
       data-bs-interval="false">
    <Slide componentDescription="SecondWarDescription"
           :sliderWithCaption="sliderWithCaption" :carouselId="carouselId" />
    <button class="carousel-control-next" type="button" :data-bs-target="'#carouselExampleCaptions-'+carouselId"
            data-bs-slide="next" @click="runAnimation">
      <DirectionArrow :componentLink="'#'+carouselId" arrowPosition="right"/>
    </button>
  </div>
</template>
<style>
.carousel-item {
  height: 100vh;
}

.fa-angle-left::before {
  font-size: 5rem;
}

.opacity-1 {
  opacity: 1 !important;
}

.item img {
  position: absolute;
  top: 0;
  left: 0;
  min-height: 300px;
}
</style>
<script>
import DirectionArrow from "@/components/Layout/DirectionArrow";
import Slide from "@/components/Carousel/Slide";
import {createAnimation} from "@/assets/js/animation";

export default {
  components: {Slide, DirectionArrow},
  props: ['sliderWithCaption','carouselId','componentLink'],
  methods: {
    runAnimation() {
      createAnimation();
    }
  },
  mounted() {
    createAnimation();
  }
}
</script>