<template>
  <div class="container-fluid start" ref="startContainer">
      <div class="row h-100">
          <div class="offset-4 col-4 h-100">
              <div class="d-flex flex-column justify-content-center align-items-center h-100">
                  <h1 class="text-white text-center">
                      <span class="fw-bold d-block logo-title">1944</span>
                      <span class="fw-bold d-block text-uppercase logo-subtitle">Monte Cassino</span>
                  </h1>
                  <LanguageSelector />
                  <button type="button" class="enter-button text-white" @click="enter">{{ enterText }}</button>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import { START } from "@/assets/js/Consts/Start";
import LanguageSelector from "@/components/Layout/LanguageSelector";

export default {
  components: {
    LanguageSelector,
  },
  data() {
    return {
      lang: this.$store.state.lang,
      enterText: null,
      entered: false,
    };
  },
  created() {
    this.enterText = START[this.lang].enterText;
  },
  methods: {
    enter() {
      this.entered = true;
      this.$store.commit('setEntered', this.entered);
    }
  },
  watch: {
    '$store.state.lang': function (lang) {
      this.lang = lang;
      this.enterText = START[this.lang].enterText;
    },
  }
};
</script>

<style>
.start {
  background-image: url('./../../assets/background.jpg');
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: 100%;
}

.logo-title {
  font-size: 180px;
  line-height: 1;
}

.logo-subtitle {
  font-size: 48px;
  line-height: 1;
}

.enter-button {
  display: block;
  font-size: 16px;
  text-decoration: none;
  text-transform: none;
  outline: none;
  margin: 20px 0 0;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid #fff;
  cursor: pointer;
  border-radius: 2px;
  height: 50px;
  width: 220px;
  background: #1b1b1b;
}
</style>
