    return {
      animationIsVisible: false,
      lang: this.$store.state.lang,
      content: WELCOME.pl,
    }
  },
  created() {
    this.content = WELCOME[this.lang];
  },
  props: [
    'componentLink'
  ],
  components: {Modal, DirectionArrow},
  watch: {
    '$store.state.lang': function (lang) {
      this.lang = lang;
      this.content = WELCOME[this.lang];
    },
  }
