<template>

  <div class="d-flex min-vh-100 position-relative scroll-section"
       :data-section-name="page.section"
       v-for="(page,key) in pages[this.lang]"
       :key="key">
    <keep-alive>
      <component :is="page.component"
                 :componentLink="key<=pages[this.lang].length-2?pages[this.lang][key+1].link:'#'" :readMore="page.read_more ?? ''"/>
    </keep-alive>

  </div>
</template>
<style>
.btn-orange {
  background-color: #bc4e38 !important;
  border-color: #bc4e38 !important;
  color: #fff !important;
  transition: 1s;
}

.btn-orange:hover {
  background-color: #e06a53 !important;
  border-color: #e06a53 !important;
  transition: 1s;
}
</style>
<script>
import {spyScroll} from "@/assets/js/spyscroll";
export default {
  name: 'Content',
  props: ['pages', 'lang'],
  mounted() {
    spyScroll();
  }
}
</script>