<template>
  <div class="header-menu">
    <div id="myNav" class="overlay">
      <a class="closebtn" @click="closeNav()">&times;</a>
      <div class="overlay-content">
        <div class="d-flex flex-column">
          <ul class="list-inline mx-auto justify-content-center">
            <li class="list-item text-start" v-for="page in topPages" :key="page.title">
              <a class="nav-link link-size link-color" @click="openSubmenu(page)">
                {{ page.title }}
              </a>
              <div v-if="isDropdown(page)"
                   :class="isDropdown(page) +' mx-4 display-none'" id="monte-cassino">
                <a v-for="subpage in page.subpages" :key="subpage"
                   class="nav-link link-size link-color">
                  {{ subpage.title }}
                </a>
              </div>
            </li>
          </ul>

          <div class="d-block mx-auto" style="margin-top: 160px;">
            <span class="d-block text-start link-color fs-6 mb-3">{{ partners.title }}:</span>
            <div class="partners">
              <img src="./../../assets/partners/Cassino_1.png" alt="Cassino" />
              <img src="./../../assets/partners/LOGO-Służymy-Niepodległej.png" alt="Służymy Niepodległej" />
              <img src="./../../assets/partners/logo-aprocis.png" alt="APROCIS" />
            </div>
          </div>
        </div>
      </div>
    </div>

    <button class="navbar-toggler position-fixed" @click="openNav()">
      <i class="fas fa-bars toogler-icon"></i>
    </button>
  </div>
</template>
<script>
import { HEADER_MENU } from '@/assets/js/Consts/HeaderMenu';

export default {
  props: ['lang'],
  data() {
    return {
      ...HEADER_MENU[this.lang],
    };
  },
  methods: {
    isDropdown(page) {
      return page.subpages.length === 0 ? '' : 'dropdown';
    },
    openNav() {
      document.getElementById("myNav").style.width = "100%";
    },
    closeNav() {
      document.getElementById("myNav").style.width = "0%";
    },
    openSubmenu(page) {
      let hasSubmenu = this.isDropdown(page);
      if (hasSubmenu) {
        document.getElementById("monte-cassino").classList.toggle('d-block');
      }
    },
  }
}

</script>
<style lang="scss">

$tooglerColor: #bc4e38;
$iconSize: 20px;
$topMenuBgColor: #364b51;

.top-5rem {
  top: 5rem !important;
}

#monte-cassino {
  transition: 1s;
}

.dropdown .nav-link {
  font-size: 1rem;
}

.display-none {
  display: none;
}

.navbar-toggler {
  color: $tooglerColor;
  border: 5px solid $tooglerColor !important;
  z-index: 4;
  top: 1rem;
  right: 1rem;
}

.toogler-icon {
  color: $tooglerColor;
  font-size: $iconSize;
  z-index: 1;
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 5;
  left: 0;
  top: 0;
  background-color: rgba(63, 76, 82, 0.97);
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 20%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 1.5rem;
  color: #818181;
  display: block;
  transition: 0.3s;
  cursor: pointer;
}

.overlay a:hover {
  color: $tooglerColor !important;
  transition: 1s;
}

.overlay a:focus {
  color: $tooglerColor;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
  cursor: pointer;
}

.closebtn:hover {
  color: $tooglerColor;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}

.partners {
  display: flex;
  justify-content: center;
}

.partners img {
  display: block;
  max-width: 100%;
  height: 80px;
  padding-right: 40px;
}

.partners-img:last-of-type {
  padding-right: 0;
}

</style>