<template>
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog  modal-xl modal-dialog-scrollable modal-dialog-centered" role="document">
      <div class="modal-content image-background-modal text-white">
        <div class="modal-header border-0 justify-content-end">
          <a class="modal-close pe-2" data-bs-dismiss="modal" aria-label="Close">&times;</a>
        </div>
        <div class="modal-body align-content-start">
          <component :is="this.modals[this.componentTitle]" :lang="this.lang"/>
        </div>
        <div class="modal-footer border-0 d-flex justify-content-center">
          <img height="70" src="../../assets/img/scroll.png" alt=""/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {Modals} from "@/assets/js/Consts/Modal";

export default {
  props: ['componentName', 'modalId', 'lang'],
  data() {
    return {
      componentTitle: '',
      modals: Modals
    }
  },
  mounted() {
    let $vm = this;
    let $ = require('jquery');
    $('[data-bs-toggle="modal"]').on('click', function (e) {
      let $target = $(e.target);
      $vm.componentTitle = $target.data('myvalue');
    });
  }
}
</script>
<style>
.modal-dialog-scrollable .modal-body::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

.modal-dialog-scrollable .modal-body::-webkit-scrollbar-thumb {
  background: #f00;
}
</style>