<template>
  <div class="col-12 col-xl-1 pt-3 position-fixed vh-100 z-index10 sidebar-background">
    <div class="row">
      <div class="col-4 d-xl-none">
        <button class="navbar-toggler " type="button" data-bs-toggle="collapse"
                data-bs-target="#collapseExample"
                aria-expanded="false" aria-controls="collapseExample">
          <i class="fas fa-bars toogler-icon"></i>
        </button>
      </div>
      <div class="col-4 d-xl-none">
        <a href="/">
                    <span>
                      1940
                    </span>
          <span>
                      MONTE CASSINO
                    </span>
        </a>
      </div>
      <div class="col-4">
        <HeaderMenu :lang="this.lang" />
      </div>
      <div class="collapse col-12 px-lg-0" :class="isMobile()" id="collapseExample">
        <nav class="nav">
          <ul class="navbar-nav mobile-width w-100 h-100 d-flex justify-content-center">
            <li class="nav-item d-block">
              <a class="d-none d-lg-block mb-4 text-decoration-none text-white" href="/">
                <div class="row">
                  <div class="col-12">
                    <span style="font-size: 3vw;" class="fw-bold">
                      1944
                    </span>
                  </div>
                  <div class="col-12" style="margin-top:-1rem;">
                    <span class="fw-bold">
                       MONTE CASSINO
                    </span>
                  </div>
                </div>
              </a>
            </li>
            <li class="nav-item d-block sidebar">
              <LanguageSelector/>
            </li>
          </ul>
        </nav>
        <nav class="nav" id="left-menu">
          <ul class="navbar-nav mobile-width w-100 h-100 d-flex justify-content-center">
            <li class="nav-item sidebar px-2"
                v-for="link in this.pages[this.lang]" :id="createElementId(link.link)" :key="link.component">
              <a :href="link.link"
                 :class="setActiveLink(link)"
                 @click="selectItem(link.component)"
                 class="link-color ">
                <button class="btn d-none d-lg-flex justify-content-center link-color pb-3"
                        :data-hover="link.title">

                  <div class="d-none d-lg-block diamond" id="diamond"/>
                </button>
                <span class="d-flex d-lg-none justify-content-center link-color">
                                    {{ link.title }}
                                </span>
              </a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </div>
</template>
<script>

import HeaderMenu from "@/components/Layout/HeaderMenu";
import LanguageSelector from "@/components/Layout/LanguageSelector";

export default {
  props: ['pages', 'lang'],
  data() {
    return {
      activeItem: '#welcome',
    }
  },
  components: {
    HeaderMenu,
    LanguageSelector,
  },
  methods: {
    selectItem(item) {
      this.activeItem = item;
    },
    setActiveLink(page) {
      return this.currentPageIsActiveItem(page) ? 'activeLink activeButton' : '';
    },
    currentPageIsActiveItem(page) {
      return page.component === this.activeItem || page.link === this.activeItem;
    },
    isMobile() {
      return window.innerWidth < 1024 ? "" : "show";
    },
    createElementId(currentElementLink) {
      let id = currentElementLink.replace('#', '');
      return 'sidebar' + id;
    },
  },
  watch: {
    '$store.state.activeComponent': function () {
      this.activeItem = this.$store.state.activeComponent;
    },
  }
}
</script>
<style lang="scss">
$diamondColor: #cc7761;
.sidebar-background {
  background-color: rgb(25 22 22 / 80%) !important;
}

.sidebar button:hover {
  cursor: pointer
}

.sidebar button {
  background: transparent;
  outline: none;
  position: relative;
  line-height: 1;
  overflow: hidden;
  width: 100%;
  padding-bottom: 1.25rem !important;
}

.sidebar button:focus, .sidebar button:active {
  outline: none !important;
  box-shadow: none !important;
}

.activeButton button::before {
  opacity: 1 !important;
  transform: translate(0, 0) !important;
  color: white;
}

.activeButton .diamond {
  opacity: 0 !important;
}

.sidebar button:hover::before {
  opacity: 1;
  transform: translate(0, 0);
}

.sidebar button::before {
  content: attr(data-hover);
  position: absolute;
  left: 0;
  width: 100%;
  font-weight: 700;
  font-size: .85em;
  opacity: 0;
  transform: translate(-100%, 0);
  transition: all .3s ease-in-out;
}

.sidebar button:hover div {
  opacity: 0;
  transform: translate(100%, 0)
}

.sidebar button div {
  transition: all .3s ease-in-out;
}

#diamond {
  transform: scale(0.6);
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-bottom-color: rgba(255, 255, 255, 0.4);
  position: relative;
  top: -4px;
}

#diamond::after {
  content: '';
  position: absolute;
  left: -8px;
  top: 8px;
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-top-color: rgba(255, 255, 255, 0.4);
}

.z-index10 {
  z-index: 10;
}

.activeLink {
  color: $diamondColor !important;
  transition: 1s !important;

  #diamond {
    border-bottom-color: $diamondColor !important;
    transition: 1s !important;
  }

  #diamond::after {
    border-top-color: $diamondColor !important;
    transition: 1s !important;
  }
}

.mb-035 {
  margin-bottom: 1.3rem;
}

.link-size {
  font-size: 0.75rem;
  font-weight: 700;
}

.link-color {
  color: rgba(255, 255, 255, 0.4) !important;
  text-decoration: auto;

  &:hover {
    color: $diamondColor;

    > #diamond {
      border-bottom-color: $diamondColor;
    }

    > #diamond::after {
      border-top-color: $diamondColor;
    }
  }
}

.sidebar .language-selector {
  margin: 0 0 20px 4px;
  position: relative;
  width: 150px;
  height: 50px;
}

.sidebar .language-selector .language-list li {
  padding: 0 0 0 53px;
  justify-content: initial;
}
</style>