function checkIfUserIsInTheView(e,i,n){let t=n.height(),o=n.scrollTop(),r=o+t;e.each(i,function(){let i=e(this),n=e(i).outerHeight(),t=e(i).offset().top;t+n>=o&&t<=r?i.addClass("in-view"):i.removeClass("in-view")})}function createAnimation(){let e=require("jquery");e(document).ready(function(){let i=e.find(".animation-element"),n=e(window);e(window).on("scroll resize",()=>{checkIfUserIsInTheView(e,i,n)}),e(window).trigger("scroll")})}export{createAnimation};