<template>
  <div v-if="entered" class="container-fluid">
    {{ this.runPreloader() }}
    <div class="row">
      <Sidebar :pages="PAGES" :lang="this.lang"/>
      <div class="col-12 px-0">
        <div id="main">
          <Home :pages="PAGES" :lang="this.lang"/>
        </div>
      </div>
    </div>
  </div>
  <Start v-else/>
</template>

<script>
import Sidebar from "@/components/Layout/Sidebar";
import Start from "@/components/Layout/Start";
import {Pages} from "@/assets/js/Consts/Pages"
import Home from "@/components/Pages/Content"

export default {
  name: 'App',
  components: {
    Sidebar,
    Home,
    Start,
  },
  data() {
    return {
      PAGES: Pages,
      lang: this.$store.state.lang,
      entered: this.$store.state.entered,
      prealoder: true
    };
  },
  methods: {
    runPreloader() {
      let loader = this.$loading.show(
              {
                backgroundColor: '#000',
                opacity:1,
                color:'#fff',
                loader:'dots'
              }
      );
      setTimeout(() => {
        loader.hide();
      }, 1000);
    }
  },
  watch: {
    '$store.state.entered': function (entered) {
      this.entered = entered;
    },
    '$store.state.lang': function (lang) {
      this.lang = lang;
    },
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Brygada+1918&display=swap');

html,
body {
  margin: 0;
  box-sizing: border-box;
  background: #202020 !important;
}

#app {
  font-family: 'Brygada 1918';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
}

